:root {
  --dropdown-label-color: #0fc;
  --dropdown-text-color: #0fc;
  --button-color: #0fc;
  --title-color: #0fc;
  --drop-off-header-text-color: #0fc;
  --table-header-text-color: #0fc;
  --pickup-text-color: #0fc;
  --commodity-text-color: #0fc;
  --amount-text-color: #0fc;
  --row-text-color: #0fc;
  --mission-text-color: #0fc;
  --table-outline-color: #0fc;
  --background-color: #0d0d0d;
  --secondary-background: #1a1a1a;
  --border-color: #333;
}

body {
  background-color: var(--background-color);
  color: var(--dropdown-text-color);
  width: 1200px;
  height: 800px;
  margin: 0 auto;
  padding: 0;
  font-family: Orbitron, sans-serif;
  transition: background-color .3s, color .3s;
}

.dark-mode {
  background-color: var(--background-color);
  color: var(--dropdown-text-color);
}

header {
  background-color: var(--secondary-background);
  border-bottom: 1px solid var(--border-color);
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
  box-shadow: 0 4px 8px #0003;
}

header h1 {
  color: var(--title-color);
  margin: 0;
  font-size: 24px;
}

.main-container {
  background-color: var(--background-color);
  min-height: 100vh;
  padding: 20px;
}

h1, h2, h3, h4, h5, h6, p, span, a, div, label, button, input, select, textarea, .glow-text, .text-glow, .glowing-text, table, th, td, tr, input, select, textarea, button {
  text-shadow: none !important;
}

button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 7px 20px;
  font-size: 16px;
  transition: background-color .3s, color .3s;
}

button:hover {
  background-color: #0c9;
}

.unlock-mission-button {
  margin-left: 40px;
}

.add-entry-button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  text-align: center;
  white-space: nowrap;
  border: none;
  border-radius: 5px;
  margin: 10px 0;
  padding: 7px 20px;
  font-family: inherit;
  font-size: 16px;
  text-decoration: none;
  transition: background-color .3s, color .3s;
  display: inline-block;
}

.add-entry-button:hover {
  color: #0d0d0d;
  background-color: #0c9;
}

.add-entry-button:disabled {
  color: #999;
  cursor: not-allowed;
  background-color: #666;
}

.add-entry-button:active {
  transform: scale(.98);
}

.clear-log-button {
  background-color: #f66;
  width: 150px;
  height: 35px;
}

.clear-log-button:hover {
  background-color: #f33;
}

.process-orders-button {
  background-color: var(--button-color);
  flex-wrap: nowrap;
  width: 180px;
  height: 35px;
}

.process-orders-button:hover {
  background-color: #09c;
}

.main-tabs {
  background-color: var(--secondary-background);
  border-bottom: 1px solid var(--border-color);
  justify-content: space-around;
  padding: 10px;
  display: flex;
}

.main-tab {
  cursor: pointer;
  color: var(--dropdown-text-color);
  padding: 10px 20px;
  transition: color .3s, border-bottom .3s;
}

.main-tab:hover {
  color: #0c9;
}

.active-main-tab {
  border-bottom: 2px solid var(--dropdown-text-color);
  font-weight: bold;
}

.tabs {
  background-color: var(--secondary-background);
  border-bottom: 1px solid var(--border-color);
  justify-content: space-around;
  padding: 10px;
  display: flex;
}

.tab {
  cursor: pointer;
  color: var(--dropdown-text-color);
  border-radius: 5px;
  transition: color .3s, border-bottom .3s;
}

.tab:hover {
  color: #0c9;
}

.active-tab {
  border-bottom: 2px solid var(--dropdown-text-color);
  font-weight: bold;
}

.tab-content input[type="color"] {
  border-radius: 0;
  width: 20px;
  height: 20px;
}

.form-group label:not(.preferences label) {
  color: var(--dropdown-label-color);
  text-align: center;
}

.form-group label[for="location-type"] {
  white-space: nowrap;
}

select.location-type, select.location, select.moon, select.drop-off, select.commodity {
  color: var(--dropdown-text-color);
  background-color: #333;
  border: 1px solid #333;
  border-radius: 5px;
  padding: 10px;
  font-family: Orbitron, sans-serif;
}

select.location-type {
  width: 85px;
}

select.location, select.moon {
  width: 110px;
}

select.drop-off {
  width: 300px;
}

select.commodity {
  width: 120px;
}

.form-row {
  align-items: center;
  gap: 10px;
  display: flex;
}

.react-select__control {
  color: var(--dropdown-text-color);
  background-color: #333;
  border: 1px solid #333;
  border-radius: 5px;
  padding: 5px;
  font-family: Orbitron, sans-serif;
  font-size: 14px;
}

.react-select__menu {
  color: var(--dropdown-text-color);
  background-color: #333;
  width: auto;
}

.react-select__option {
  color: var(--dropdown-text-color);
  background-color: #333;
}

.react-select__option--is-focused {
  background-color: #444;
}

.react-select__option--is-selected {
  background-color: #555;
}

.react-select__single-value {
  color: var(--dropdown-text-color);
}

.react-select__menu-list {
  scrollbar-width: none;
  -ms-overflow-style: none;
  width: auto;
}

.react-select__menu-list::-webkit-scrollbar {
  display: none;
}

.react-select__input input {
  color: #fff;
}

.first-dropdown-select .react-select__control, .second-dropdown-select .react-select__control {
  width: 350px;
}

.location-type-select .react-select__control {
  width: 90px;
}

.planet-select .react-select__control {
  width: 119px;
}

.moon-select .react-select__control {
  width: 110px;
}

.drop-off-select .react-select__control {
  width: 300px;
}

.commodity-select .react-select__control {
  width: 200px;
}

.station-select .react-select__control {
  width: 119px;
}

.table-container {
  border-radius: 10px;
  margin-top: 20px;
  overflow-x: auto;
}

.hauling-manifest-table th, .history-table th {
  background-color: var(--secondary-background);
  color: var(--table-header-text-color);
  text-align: center;
  border: 1px solid var(--table-outline-color);
  text-transform: uppercase;
  padding: 12px;
  font-size: 14px;
  font-weight: bold;
}

.hauling-manifest-table {
  border-collapse: separate;
  background-color: var(--secondary-background);
  border: 1px solid var(--table-outline-color);
  border-radius: 10px;
  width: 100%;
  margin-bottom: 20px;
  overflow: hidden;
  box-shadow: 0 4px 8px #0003;
}

.hauling-manifest-table th {
  color: var(--table-header-text-color);
  text-align: center;
  border: 1px solid var(--table-outline-color);
  text-transform: uppercase;
  background-color: #1a1a1a;
  padding: 12px;
  font-size: 14px;
  font-weight: bold;
}

.hauling-manifest-table td {
  text-align: center;
  border: 1px solid var(--table-outline-color);
  color: var(--row-text-color);
  padding: 8px;
  line-height: 1.2;
}

.hauling-manifest-table th.pickup, .hauling-manifest-table td.pickup {
  white-space: nowrap;
  width: 33%;
}

.hauling-manifest-table th.commodity, .hauling-manifest-table td.commodity {
  white-space: nowrap;
  width: 19%;
}

.hauling-manifest-table th.amount, .hauling-manifest-table td.amount {
  white-space: nowrap;
  width: 100px;
}

.hauling-manifest-table th.actions, .hauling-manifest-table td.actions {
  white-space: nowrap;
  width: auto;
}

.hauling-manifest-table th.status, .hauling-manifest-table td.status {
  white-space: nowrap;
  width: 8.2%;
}

.hauling-manifest-table tr:hover {
  background-color: #00ffcc1a;
}

.hauling-manifest-table tr:last-child td {
  border-bottom: none;
}

.hauling-manifest-table .actions button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 13px;
  transition: background-color .3s;
}

.hauling-manifest-table .actions button:hover {
  background-color: #0c9;
}

.hauling-manifest-table .actions .remove-cargo-button {
  color: #0d0d0d;
  background-color: #f66;
  font-size: 12px;
}

.hauling-manifest-table .actions .remove-cargo-button:hover {
  background-color: #f33;
}

.hauling-manifest-table .status-pending {
  color: orange;
}

.hauling-manifest-table .status-delivered {
  color: #0f0;
}

.history-table {
  border-collapse: separate;
  background-color: var(--secondary-background);
  border: 1px solid var(--table-outline-color);
  border-radius: 5px;
  width: 100%;
  margin-top: 10px;
  overflow: hidden;
}

.history-table td {
  text-align: center;
  border: 1px solid var(--table-outline-color);
  color: var(--row-text-color);
  padding: 8px;
  font-size: 14px;
}

.hauling-manifest-table td:hover, .history-table td:hover {
  background-color: #00ffcc0d;
}

.payouts-table {
  border-collapse: separate;
  border: 1px solid var(--table-outline-color);
  background-color: #1a1a1a;
  border-radius: 5px;
  width: 100%;
  overflow: hidden;
}

.payouts-table th, .payouts-table td {
  text-align: left;
  border: 1px solid var(--table-outline-color);
  padding: 6px;
}

.payouts-table th {
  color: var(--table-header-text-color);
  background-color: #333;
  padding: 8px;
  font-size: 14px;
  font-weight: bold;
}

.payouts-table td {
  color: var(--row-text-color);
  padding: 4px;
  font-size: 12px;
}

.payouts-table tr:hover {
  background-color: #333;
}

.payouts-table th:last-child, .payouts-table td:last-child {
  text-align: center;
  width: 80px;
  color: var(--row-text-color);
  padding: 4px 8px;
}

.payouts-table td:last-child {
  transition: background-color .3s;
}

.payouts-table td:last-child[style*="background-color: #ff44444d"]:hover {
  background-color: #f446 !important;
}

.drop-off-header {
  background-color: var(--secondary-background);
  border: 1px solid var(--border-color);
  cursor: pointer;
  color: var(--drop-off-header-text-color);
  outline: 1px solid var(--table-outline-color);
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 20px;
  transition: background-color .3s, color .3s;
  display: flex;
}

.drop-off-header:hover {
  color: #0c9;
  background-color: #333;
}

.drop-off-header .left-box {
  align-items: center;
  gap: 10px;
  display: flex;
}

.drop-off-header .left-box button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 14px;
  transition: background-color .3s, color .3s;
}

.drop-off-header .left-box button:hover {
  background-color: #0c9;
}

.drop-off-header .right-box {
  align-items: center;
  gap: 10px;
  display: flex;
}

.drop-off-header .right-box span {
  color: var(--dropdown-text-color);
  background-color: #333;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 14px;
}

.drop-off-header .right-box button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 14px;
  transition: background-color .3s, color .3s;
}

.drop-off-header .right-box button:hover {
  background-color: #0c9;
}

.drop-off-header .right-box .ocr-mission-payout-capture {
  max-width: 120px;
  color: var(--dropdown-text-color);
  text-align: left;
  background-color: #333;
  border: 1px solid #555;
  border-radius: 4px;
  padding: 4px 8px;
  font-size: 14px;
}

.drop-off-header .right-box .ocr-mission-payout-capture:focus {
  border-color: var(--button-color);
  box-shadow: 0 0 0 1px var(--button-color);
  outline: none;
}

.drop-off-header .right-box .ocr-mission-payout-capture::placeholder {
  color: #666;
}

.table {
  border-collapse: separate;
  border-spacing: 0;
  background-color: #1a1a1a;
  width: 100%;
  margin: 0;
}

.mission-group {
  border: 2px solid var(--table-outline-color);
  background-color: #1a1a1a;
  border-radius: 5px;
  margin-bottom: 20px;
  overflow: hidden;
}

.mission-header {
  border-bottom: 2px solid var(--table-outline-color);
  color: var(--mission-text-color);
  background-color: #1a1a1a;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

.hauling-mission-table {
  border-collapse: separate;
  border-spacing: 0;
  border: 1px solid var(--table-outline-color);
  background-color: #262626;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 0;
  overflow: hidden;
}

.hauling-mission-table th {
  color: var(--table-header-text-color);
  text-align: left;
  border: 1px solid var(--table-outline-color);
  text-transform: uppercase;
  background-color: #1a1a1a;
  padding: 10px 12px;
  font-size: 14px;
  font-weight: bold;
}

.hauling-mission-table td {
  border: 1px solid var(--table-outline-color);
  color: var(--row-text-color);
  padding: 8px 12px;
  line-height: 1.2;
}

.hauling-mission-table th.pickup, .hauling-mission-table td.pickup {
  white-space: nowrap;
  width: 35%;
}

.hauling-mission-table th.commodity, .hauling-mission-table td.commodity {
  white-space: nowrap;
  width: 25%;
}

.hauling-mission-table th.amount, .hauling-mission-table td.amount {
  white-space: nowrap;
  text-align: center;
  width: 15%;
  color: var(--row-text-color);
  background-color: #1a1a1a;
  padding: 4px 8px;
  font-family: Orbitron, sans-serif;
}

.hauling-mission-table th.actions, .hauling-mission-table td.actions {
  white-space: nowrap;
  width: auto;
}

.hauling-mission-table th.status, .hauling-mission-table td.status {
  white-space: nowrap;
  width: 10%;
}

.hauling-mission-table tr {
  line-height: 1.2;
}

.hauling-mission-table tr:hover {
  background-color: #00ffcc0d;
}

.hauling-mission-table tr:last-child td {
  border-bottom: none;
}

.hauling-mission-table .status-pending {
  color: orange;
}

.hauling-mission-table .status-delivered {
  color: #0f0;
}

.hauling-mission-table .actions button {
  background-color: var(--button-color);
  color: #0d0d0d;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  margin: 0 2px;
  padding: 5px 10px;
  font-size: 13px;
  transition: background-color .3s;
}

.hauling-mission-table .actions button:hover {
  background-color: #0c9;
}

.hauling-mission-table .actions .remove-cargo-button {
  color: #0d0d0d;
  background-color: #f44;
}

.hauling-mission-table .actions .remove-cargo-button:hover {
  background-color: #f66;
}

.hauling-mission-table .actions input[type="text"] {
  width: 60px;
  color: var(--dropdown-text-color);
  text-align: center;
  background-color: #333;
  border: 1px solid #444;
  border-radius: 4px;
  padding: 4px 8px;
  font-size: 13px;
}

.hauling-mission-table-container {
  border: 1px solid var(--table-outline-color);
  border-radius: 5px;
  margin: 20px 0;
  overflow: hidden;
  box-shadow: 0 2px 4px #0003;
}

.hauling-mission-table th.amount {
  text-align: center;
  color: var(--table-header-text-color);
  text-transform: uppercase;
  background-color: #1a1a1a;
  font-weight: bold;
}

.hauling-mission-table td.amount {
  white-space: nowrap;
  text-align: center;
  width: 15%;
  color: var(--row-text-color);
  background-color: #1a1a1a;
  padding: 4px 8px;
  font-family: Orbitron, sans-serif;
}

body {
  transform-origin: 0 0;
  width: 100%;
  max-width: 1200px;
  min-height: 800px;
  margin: 0;
  transition: transform .3s;
}

@media screen and (width <= 1125px) {
  body {
    width: 1200px;
    transform: scale(1);
  }
}

@media screen and (width <= 1100px) {
  body {
    transform: scale(.98);
  }
}

@media screen and (width <= 1075px) {
  body {
    transform: scale(.96);
  }
}

@media screen and (width <= 1050px) {
  body {
    transform: scale(.94);
  }
}

@media screen and (width <= 1025px) {
  body {
    transform: scale(.92);
  }
}

@media screen and (width <= 1000px) {
  body {
    transform: scale(.9);
  }
}

@media screen and (width <= 975px) {
  body {
    transform: scale(.88);
  }
}

@media screen and (width <= 950px) {
  body {
    transform: scale(.86);
  }
}

@media screen and (width <= 925px) {
  body {
    transform: scale(.84);
  }
}

@media screen and (width <= 900px) {
  body {
    transform: scale(.82);
  }
}

@media screen and (width <= 875px) {
  body {
    transform: scale(.8);
  }
}

@media screen and (width <= 850px) {
  body {
    transform: scale(.78);
  }
}

@media screen and (width <= 825px) {
  body {
    transform: scale(.76);
  }
}

@media screen and (width <= 800px) {
  body {
    transform: scale(.74);
  }
}

@media screen and (width <= 775px) {
  body {
    transform: scale(.72);
  }
}

@media screen and (width <= 750px) {
  body {
    transform: scale(.7);
  }
}

@media screen and (resolution >= 192dpi) {
  body {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}

.debug-popup {
  border: 1px solid var(--table-outline-color);
  z-index: 1000;
  background-color: #1a1a1a;
  border-radius: 5px;
  width: 400px;
  position: fixed;
  box-shadow: 0 4px 8px #0000004d;
}

.debug-popup-header {
  border-bottom: 1px solid var(--table-outline-color);
  cursor: move;
  user-select: none;
  background-color: #333;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

.debug-popup-header button {
  color: var(--dropdown-text-color);
  cursor: pointer;
  background: none;
  border: none;
  padding: 0 5px;
  font-size: 20px;
}

.debug-popup-header button:hover {
  color: #f66;
}

.debug-popup-content {
  max-height: 500px;
  padding: 15px;
  overflow-y: auto;
}

.debug-section {
  margin-bottom: 15px;
}

.debug-section h4 {
  color: var(--dropdown-text-color);
  border-bottom: 1px solid #444;
  margin-bottom: 5px;
  padding-bottom: 5px;
}

.debug-section pre {
  color: var(--dropdown-text-color);
  white-space: pre-wrap;
  word-break: break-all;
  background-color: #2a2a2a;
  border-radius: 3px;
  margin: 0;
  padding: 10px;
  font-family: monospace;
  font-size: 12px;
}

.tooltip-popup {
  border: 1px solid var(--table-outline-color);
  z-index: 1000;
  resize: both;
  background-color: #1a1a1a;
  border-radius: 5px;
  flex-direction: column;
  display: flex;
  position: fixed;
  box-shadow: 0 4px 8px #0000004d;
}

.tooltip-popup-header {
  border-bottom: 1px solid var(--table-outline-color);
  cursor: move;
  user-select: none;
  background-color: #333;
  justify-content: space-between;
  align-items: center;
  height: 20px;
  padding: 10px;
  display: flex;
}

.tooltip-popup-header button {
  color: var(--dropdown-text-color);
  cursor: pointer;
  background: none;
  border: none;
  padding: 0 5px;
  font-size: 20px;
}

.tooltip-popup-header button:hover {
  color: #f66;
}

.tooltip-popup-content {
  flex: 1;
  height: calc(100% - 50px);
  min-height: 0;
  padding: 15px;
  overflow-y: auto;
}

.tooltip-popup-content p {
  color: var(--dropdown-text-color);
  word-wrap: break-word;
  margin: 0 0 10px;
  line-height: 1.4;
}

.tooltip-popup-content p:last-child {
  margin-bottom: 0;
}

.tooltip-popup-resize-handle {
  cursor: se-resize;
  background: linear-gradient(135deg, transparent 0%, transparent 50%, var(--dropdown-text-color) 50%, var(--dropdown-text-color) 100%);
  opacity: .3;
  width: 15px;
  height: 15px;
  transition: opacity .2s;
  position: absolute;
  bottom: 0;
  right: 0;
}

.tooltip-popup-resize-handle:hover {
  opacity: .8;
}

.tab-container {
  display: inline-block;
  position: relative;
}

.tab-info-icon {
  color: var(--dropdown-text-color);
  opacity: .7;
  cursor: pointer;
  vertical-align: middle;
  margin-left: 8px;
  padding-bottom: 2px;
  font-size: 18px;
  display: inline-block;
